	module.exports = {"data":{"site":{"siteMetadata":{"title":"ALEX P . X - lirawx.me","subtitle":"Life is short, let it be undefined.","copyright":"© 2018 All rights reserved.","author":{"name":"Lirawx.","twitter":"lira_wx"},"disqusShortname":"lirawx","url":"https://lirawx.me"}},"markdownRemark":{"id":"/home/travis/build/lirawx/lirawx.github.io/src/pages/articles/2018-09-01---How-to-deploy-npm-package/index.md absPath of file >>> MarkdownRemark","html":"<h2>environment</h2>\n<ul>\n<li>node</li>\n<li>npm</li>\n<li>nrm</li>\n</ul>\n<h3>change registry</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code class=\"language-bash\">nrm use <span class=\"token function\">npm</span></code></pre>\n      </div>\n<h3>npm add a user</h3>\n<p>npm add-user</p>\n<p>name:\npassword\nemail:</p>\n<blockquote>\n<p>then you will receive a email to verify your email, Please check your junk mail list .</p>\n</blockquote>\n<h3>GitHub account</h3>\n<p>you must have a git repo to contain npm package source code.</p>\n<h3>local files</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-plaintext\"><code class=\"language-plaintext\">├── README.md\n├── index.js\n├── lib\n│   └── index.js\n├── package.json\n└── test</code></pre>\n      </div>\n<p>this is a simple npm package template</p>\n<h3>package.json</h3>\n<p>some configure must be set </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-json\"><code class=\"language-json\"><span class=\"token punctuation\">{</span>\n  <span class=\"token property\">\"name\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"your-package-name\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"version\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"1.0.0\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"description\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"main\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"index.js\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"repository\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"your-https-git-repo\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"keywords\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">[</span>\n    <span class=\"token string\">\"hello\"</span><span class=\"token punctuation\">,</span>\n    <span class=\"token string\">\"test\"</span>\n  <span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"files\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">[</span>\n    <span class=\"token string\">\"lib\"</span><span class=\"token punctuation\">,</span>\n    <span class=\"token string\">\"README.md\"</span>\n  <span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"scripts\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token property\">\"test\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"echo \\\"Error: no test specified\\\" &amp;&amp; exit 1\"</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"author\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"your name  &lt;your email>\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"license\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"MIT\"</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<blockquote>\n<p>name and version is required.\nand author must be the same you login.</p>\n</blockquote>\n<h3>login &#x26;&#x26; publish</h3>\n<p>npm login</p>\n<p>username:\npassword:</p>\n<p>npm publish</p>\n<blockquote>\n<p>you must git commit local branch before run npm publish</p>\n</blockquote>\n<blockquote>\n<p>usually success ,and you will find your package, on <a href=\"https://npmjs.com\">npmjs.com</a></p>\n</blockquote>\n<h3>publish versions</h3>\n<p><code class=\"language-text\">$ npm version &lt;patch|minor|major&gt;</code>, if you have already published to npm</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">- patch for bug fix\n- minor for new feature\n- major for breaking changes</code></pre>\n      </div>\n<p><code class=\"language-text\">npm pack</code> or <code class=\"language-text\">npm link</code> to validate the module to be publish</p>\n<p><code class=\"language-text\">$ npm publish</code></p>","fields":{"tagSlugs":["/tags/npm/","/tags/javascript/","/tags/web-development/"]},"frontmatter":{"title":"How To Deploy A Npm Package","tags":["Npm","Javascript","Web Development"],"date":"2018-09-01T22:40:32.169Z","description":"An Essay on how to deploy a npm package to npmjs.org, just for the beginner."}}},"pathContext":{"slug":"/posts/How-to-deploy-npm-package/"}}

